.blank-line {
  margin: 0;
  font-size: var(--font-size-21xl);
  font-weight: 800;
  color: var(--color-black);
}
.witam-w-aplikacji {
  margin: 0;
  line-height: 160.5%;
}
.witam-w-aplikacji-container1 {
  line-break: anywhere;
  width: 100%;
}
.witam-w-aplikacji-container {
  position: absolute;
  top: 180px;
  left: 11px;
  display: flex;
  align-items: center;
  width: 369px;
  height: 329px;
}
.czec {
  position: absolute;
  top: 134px;
  left: 81px;
  font-size: 64px;
  font-weight: 800;
  color: var(--color-lightslategray);
}
.first-open-child,
.rectangle-div {
  position: absolute;
  border: 3px solid var(--color-lightslategray);
  box-sizing: border-box;
  width: 364px;
  height: 100px;
}
.first-open-child {
  background-color: var(--color-white);
  top: 512px;
  left: 13px;
  border-radius: var(--br-xl) var(--br-xl) 0 0;
}
.rectangle-div {
  top: 0;
  left: 0;
  border-radius: 0 0 var(--br-xl) var(--br-xl);
  background-color: transparent;
}
.rectangle-container,
.zaczynamy {
  position: absolute;
  height: 100px;
  cursor: pointer;
}
.zaczynamy {
  border: 0;
  padding: 0;
  background-color: transparent;
  top: 0;
  left: 5.58px;
  font-size: var(--font-size-21xl);
  font-family: var(--font-garet);
  color: var(--color-black);
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 358.42px;
}
.rectangle-container {
  top: 609px;
  left: 13px;
  width: 364px;
}
.first-open {
  position: relative;
  background-color: var(--color-lightgray);
  width: 100%;
  height: 844px;
  overflow: hidden;
  text-align: center;
  font-size: var(--font-size-17xl);
  color: var(--color-dimgray);
  font-family: var(--font-garet);
}
